.text-page-layout {
  display: flex;
  flex-wrap: wrap;
  margin-left: -3rem;
}

.text-page-layout > * {
  padding-left: 3rem;
}

@media (min-width: 56em) {
  .text-page-sidebar {
    width: 33.3333333333%;
  }

  .text-page-body {
    width: 66.6666666667%;
  }
}

.text-page .toc {
  background: none;
  padding: 0 1.5rem 0 0;
}

.text-page .toc h2 {
  display: none;
}

